}

.dashboard {
  background-color: #F3F4F6;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .main {
  flex: 1;
  overflow: scroll;
}
.dashboard .content {
  padding: 2rem;
}
.dashboard .content .page-name {
  font-size: 2.8rem;
  text-align: center;
}
.dashboard .form input:not([type=submit]) {
  background-color: #FFFFFF;
}
.dashboard .form input[type=submit] {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  font-size: 1.4rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .dashboard .form input[type=submit] {
    width: auto;
  }
}
.dashboard .form input[type=submit]:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.dashboard .link {
  color: #4338CA;
}
.dashboard .link:hover {
  color: #28217b;
}

.sidebar {
  opacity: 0;
  background-color: #4338CA;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 300ms;
}
.sidebar.show {
  opacity: 1;
  padding: 2rem;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
@media (min-width: 768px) {
  .sidebar.show {
    display: none;
  }
}
.sidebar.hide {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: #4338CA;
  }
}
.sidebar .sidebar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .sidebar-container h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .sidebar-container img {
  display: flex;
  justify-content: center;
  height: 3rem;
  width: 3rem;
}
.sidebar .sidebar-container img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .sidebar-container img {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  font-size: 2.4rem;
  margin-bottom: 2rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.active {
  background-color: #28217b;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    margin-bottom: 1.5rem;
    text-align: left;
    font-size: 1.6rem;
  }
}
.sidebar .log-out-mobile {
  margin-top: 10rem;
}
.sidebar .log-out-mobile .log-out {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .log-out-mobile .log-out {
    width: auto;
  }
}
.sidebar .log-out-mobile .log-out:hover {
  background-color: #352ca3;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .log-out-mobile {
    display: none;
  }
}

.mobile-bar {
  background-color: #4338CA;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .mobile-bar {
    display: none;
  }
}
.mobile-bar h1 {
  color: #FFFFFF;
  margin: 0;
}
.mobile-bar img {
  width: 1rem;
}
.mobile-bar img:hover {
  cursor: pointer;
}

.bar {
