  }
}
.bar .log-out:hover {
  background-color: #352ca3;
  cursor: pointer;
}

.projects-list {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .projects-list {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .projects-list {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.projects-list .project {
  background-color: #564ccf;
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 2rem;
  line-height: 2;
  border-radius: 0.5rem;
}
.projects-list .project:hover {
  background-color: #3b31b8;
}
.projects-list .project a {
  color: #FFFFFF;
}

.new-task-container {
  display: flex;
  justify-content: center;
}
.new-task-container .add-task {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .new-task-container .add-task {
    width: auto;
  }
}
.new-task-container .add-task:hover {
  background-color: #066981;
  cursor: pointer;
}

.tasks-list {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
}
.tasks-list .no-tasks {
  text-align: center;
}

.task {
  padding: 0.5rem 2rem;
  border-bottom: 2px solid #e4e7eb;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.task:last-of-type {
  border: none;
}
.task p {
  font-size: 1.4rem;
}
.task .options {
  display: flex;
  gap: 1rem;
}
.task .options button {
  border: none;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  font-weight: 700;
  border-radius: 1rem;
}
.task .options button:hover {
  cursor: pointer;
}
.task .options .complete {
  background-color: #2563EB;
  color: #FFFFFF;
}
.task .options .pending {
  background-color: #F59E0B;
  color: #FFFFFF;
}
.task .options .delete-task {
  background-color: rgb(169, 0, 0);
  color: #FFFFFF;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .form {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 500ms;
  transition-timing-function: ease-out;
  opacity: 0;
  border-radius: 0.5rem;
}
.modal .form legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal .form .options {
  display: flex;
  justify-content: space-between;
}
.modal .form .submit-new-task {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .modal .form .submit-new-task {
    width: auto;
  }
}
.modal .form .submit-new-task:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.modal .form .close-modal {
  background-color: #F59E0B;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .modal .form .close-modal {
    width: auto;
  }
}
.modal .form .close-modal:hover {
  background-color: #c57f08;
  cursor: pointer;
}
.modal .form.animate {
  /* This centers the modal window vertically, first we move it so it starts at the 50% of the screen and then move 50% of the content up so the 50% of the screen is at exactly half the content */
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .form.close {
  transform: translateY(-200%);
  opacity: 0;
}

